.currency-button{
    width: 30px;
    height: 30px;
    font-size: 18px;
    border: 0;
    border-radius: 20px;
    font-weight: bold;
    transition: opacity 0.05s ease-in-out;
}

.disabled-currency-button{
    width: 30px;
    height: 30px;
    font-size: 18px;
    border: 0;
    border-radius: 20px;
    font-weight: bold;
    opacity: 0.15;
    transition: opacity 0.05s ease-in-out;
    transition-delay: 0.05s
}

.currency-button:hover{
    cursor: grabbing;
}

.disabled-currency-button:hover{
    cursor: grabbing;
}

@media (max-width: 1125px) {
    .currency-button{
        width: 25px;
        height: 25px;
        font-size: 15px;
    }

    .disabled-currency-button{
        width: 20px;
        height: 20px;
        font-size: 15px;
    }
}
